// Seismic imports
import { createCustomElement } from '@servicenow/ui-core';
import react from '@servicenow/ui-renderer-react';

// 3rd party imports
import React from 'react';
import PropTypes from 'prop-types';
import { compact, get, map, omit } from 'lodash';

// Function imports
import {
	fieldPropTypes,
	ForwardProperties,
	transformState,
	withValueChangeCallbacks
} from '@devsnc/sn-controls-common';

// Component imports
import GlideQueryBase from '@devsnc/sn-glide-query-base';
import actionHandlers from './actionHandlers';

/**
 * This component allows looking up arbitrary fields in a table. This is very
 * similar to the `sn-record-reference-connected` component, but it does not point to any
 * specific instance.
 */
export class ConnectedFieldLookup extends React.PureComponent {
	static componentName = 'now-record-field-lookup-connected';
	static actionHandlers = actionHandlers;
	getVariables = inputVal => {
		const {
			additionalQueryString,
			tableName,
			fieldName,
			resultLimit
		} = this.props;

		if (tableName && fieldName) {
			const nameMatch = `${fieldName}STARTSWITH${inputVal}`;
			const defaultOrderBy = `ORDERBY${fieldName}`;

			const baseConditions = [defaultOrderBy, additionalQueryString];

			const conditions = inputVal
				? [nameMatch, ...baseConditions]
				: baseConditions;

			return {
				field: fieldName,
				table: tableName,
				paginationLimit: resultLimit,
				conditions: compact(conditions).join('^')
			};
		}
	};

	mapResults = data => {
		let realData;

		try {
			realData = JSON.parse(
				get(data, 'GlideLayout_Query.listLayout._graphql._result')
			);
		} catch (e) {
			// TODO: Beacon?
		}
		const options = get(realData, [
			'data',
			'GlideRecord_Query',
			this.props.tableName,
			'_results'
		]);
		const totalCount = get(realData, [
			'data',
			'GlideRecord_Query',
			this.props.tableName,
			'_rowCount'
		]);

		return {
			options: map(options, option => ({
				value: get(option, 'sys_id.value'),
				displayValue: get(option, [this.props.fieldName, 'displayValue'])
			})),
			totalCount
		};
	};

	render() {
		const results = get(this.props, 'properties.results');
		const isSearching = get(this.props, 'properties.isSearching');
		return (
			<GlideQueryBase
				{...omit(this.props, ['resultLimit'])}
				isSearching={isSearching}
				results={results}
				mapResults={this.mapResults}
				getVariables={this.getVariables}
			/>
		);
	}
}

ConnectedFieldLookup.propTypes = {
	...fieldPropTypes,

	disableReferenceQualifier: PropTypes.any,

	searchDebounce: PropTypes.number,

	tableName: PropTypes.any,

	referenceTable: PropTypes.any,

	fieldName: PropTypes.any,

	recordSysId: PropTypes.any,

	dependentField: PropTypes.string,

	dependentFieldValue: PropTypes.string,

	dependentValue: PropTypes.string,

	serializedChanges: PropTypes.any,

	ignoreReferenceQualifier: PropTypes.any,

	resultLimit: PropTypes.number,

	value: PropTypes.any,

	displayValue: PropTypes.oneOfType([
		PropTypes.string,
		PropTypes.number,
		PropTypes.array
	]),

	onEnter: PropTypes.func,

	multiSelection: PropTypes.bool,

	noEmail: PropTypes.bool,

	referenceAddon: PropTypes.oneOfType([
		PropTypes.func,
		PropTypes.bool,
		PropTypes.string
	]),

	searchAddon: PropTypes.oneOfType([
		PropTypes.func,
		PropTypes.bool,
		PropTypes.string
	]),

	hideReferenceIcon: PropTypes.bool,

	referenceAddonReadonly: PropTypes.bool,

	disableDisplayValueWarning: PropTypes.bool,

	formData: PropTypes.object
};

ConnectedFieldLookup.style = GlideQueryBase.style;

ConnectedFieldLookup.defaultProps = {
	multiSelection: false,
	disableReferenceQualifier: false,
	ignoreReferenceQualifier: false,
	resultLimit: 15,
	referenceAddon: true,
	searchAddon: true
};

const DefaultConnectedFieldLookup = withValueChangeCallbacks(
	{ stopBubbling: ['SN_GLIDE_QUERY_BASE'] },
	ConnectedFieldLookup
);

export default DefaultConnectedFieldLookup;

/**
 * ```jsx
 * // If you want declarative actions to work, add the dependency and add this import.
 * import '@devsnc/sn-declarative-actions';
 *
 * <now-record-field-lookup-connected
 *		tableName="sys_user"
 *		fieldName="name"
 *		value="5137153cc611227c000bbd1bd8cd2005"
 *		displayValue="Abel Tutor"
 * />
 * ```
 *
 * @seismicElement now-record-field-lookup-connected
 * @summary Now Record Field Lookup Connected Component
 *
 */

createCustomElement('now-record-field-lookup-connected', {
	type: react,
	view: ForwardProperties(DefaultConnectedFieldLookup),
	styles: ConnectedFieldLookup.style,
	properties: {
		/**
		 * Additional filter to be added to the query string. The syntax should
		 * follow the standard servicenow filter/query syntax.
		 *
		 * These conditions will added as an AND to the existing query condition.
		 *
		 * e.g. active=true
		 *
		 * @see https://docs.servicenow.com/bundle/orlando-platform-user-interface/page/use/common-ui-elements/reference/r_OpAvailableFiltersQueries.html#r_OpAvailableFiltersQueries
		 * @type {string}
		 */
		additionalQueryString: { schema: { type: 'string' }, default: '' },
		/**
		 * aria-describedby
		 * Which element (by ID) provides more description for the component
		 *
		 * @type {string}
		 */
		ariaDescribedBy: {},

		/**
		 * aria-label
		 * Accessible label for the component. Only needed when the `label` prop is not
		 * descriptive enough on its own.
		 *
		 * @type {string}
		 */
		ariaLabel: {},

		/**
		 * aria-labelledby
		 * Which element (by ID) acts as a label for the component
		 *
		 * @type {string}
		 */
		ariaLabelledby: {},

		/**
		 * Causes the component to receive focus as soon as it has finished
		 * mounting.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 *
		 * @type {(boolean|string)}
		 */
		autofocus: {},

		/**
		 * Optional name of the dependent field
		 *
		 * @type {string}
		 */
		dependentField: {},

		/**
		 * Optional value of the dependent field
		 *
		 * @type {string}
		 */
		dependentFieldValue: {},

		/**
		 * Please use `dependentFieldValue` instead
		 *
		 * @type {string}
		 * @private
		 */
		dependentValue: {},

		/**
		 * Additional content describing the field, usually to provide further
		 * instruction to the user.
		 *
		 * @type {string}
		 */
		description: {},

		/**
		 * Flag to toggle turning off display value warning
		 *
		 * @type {boolean}
		 */
		disableDisplayValueWarning: {},

		/**
		 * When true, prevent the user from typing in free-form email addresses.
		 * Instead, an email must be selected from the list of available options.
		 *
		 * @type {boolean}
		 */
		disableEmailFreeformEntry: {},

		/**
		 * displayValue can be string or number or array of values
		 *
		 * @type {(array|number|string)}
		 */
		displayValue: {},

		/**
		 * Name of the field
		 *
		 * @type {string}
		 */
		fieldName: {},

		/**
		 * @type {string}
		 * @private
		 */
		fieldType: {},

		/**
		 * formData object containing glide form data
		 *
		 * @type {object}
		 * @private
		 */
		formData: {},

		/**
		 * Flag to hide the reference icon
		 *
		 * @type {boolean}
		 */
		hideReferenceIcon: {},

		/**
		 * Boolean to toggle reference qualifiers
		 *
		 * @type {boolean}
		 */
		disableReferenceQualifier: { default: false },

		/**
		 * Please use `disableReferenceQualifier` instead
		 *
		 * @type {boolean}
		 * @private
		 */
		ignoreReferenceQualifier: { default: false },

		/**
		 * Specifies whether the current value is not valid.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 *
		 * @type {(boolean|string)}
		 */
		invalid: {},

		/**
		 * Indicates whether the component is waiting for a graphql request to finish
		 *
		 * @type {boolean}
		 */
		isSearching: {},

		/**
		 * It's a label! All form controls need a label!
		 *
		 * @type {string}
		 */
		label: {},

		/**
		 * Array of message objects that must contain type and message itself.
		 *
		 * @type {Array<{type: ("error"|"warning"|"success"|"liveUpdate"|"info"|"suggestion"), type:Array<string,Object<{string, getMessage:function}>> }>}
		 */
		messages: {},

		/**
		 * Flag that switches between multiple and single value reference inputs
		 *
		 * @type {boolean}
		 */
		multiSelection: { default: false },

		/**
		 * Name for the component. Any events dispatched by the component will use this name.
		 *
		 * @type {string}
		 */
		name: {},

		/**
		 * Please use `disableEmailFreeformEntry` instead
		 *
		 * @type {boolean}
		 * @private
		 */
		noEmail: {},

		/**
		 * Callback function triggered when the user presses Enter
		 *
		 * @type {function}
		 * @private
		 */
		onEnter: {},

		/**
		 * Callback used when an intermediate value has changed.
		 *
		 * @type {function}
		 * @private
		 */
		onStagedValueChange: {},

		/**
		 * Callback used when the value has changed. Very similar to an `onChange`
		 * event, but this may also fire when the component formats its value
		 * onBlur.
		 *
		 * @param {object} event - The event payload
		 * @param {string} event.name - The name of the component that triggered the event
		 * @param {any}    event.value - The new value for the component
		 * @param {string} event.displayValue - The human-readable represenation of the value
		 *
		 * @param {object} event.error - If the current value is invalid, this propery will be set. When the current value is valid, this property will be null.
		 * @param {string} event.error.type - Error type
		 * @param {string} event.error.message - Description of why the value is invalid
		 *
		 * @type { function }
		 * @private
		 */
		onValueChange: {},

		/**
		 * Content to display when there is no suitable display value.
		 *
		 * @type {string}
		 */
		placeholder: {},

		/**
		 * Specifies that the user cannot modify the value of the component.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 *
		 * @type {(boolean | string)}
		 */
		readonly: {},

		/**
		 * sys_id of the current record where the field is located
		 *
		 * @type {string}
		 */
		recordSysId: {},

		/**
		 * Callback to show record
		 *
		 * @type {(boolean | function | string)}
		 * @private
		 */
		referenceAddon: { default: true },

		/**
		 * Flag to show the reference icon in readonly mode
		 *
		 * @type {boolean}
		 */
		referenceAddonReadonly: {},

		/**
		 * Name of the table the record is listed in
		 *
		 * @type {string}
		 */
		referenceTable: {},

		/**
		 * Whether or not the user must fill in a value for this component.
		 * Can be specified as a boolean or the string "true"
		 *
		 * @type {(boolean | string)}
		 */
		required: {},

		/**
		 * Integer limit of choice suggestions
		 *
		 * @type {number}
		 */
		resultLimit: { default: 15 },

		/**
		 * The results returned from glide query base
		 *
		 * @type {array}
		 */
		results: { default: [] },

		/**
		 * Specifies that the field should be rendered right-to-left.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 *
		 * @type {(boolean | string)}
		 * @private
		 */
		reversed: {},

		/**
		 * Callback to launch reference list modal
		 *
		 * @type {(boolean | function | string)}
		 * @private
		 */
		searchAddon: { default: true },

		/**
		 * Integer to set the debounce interval for hitting the api when searching reference fields
		 * Defaults to 400ms
		 *
		 * @type {number}
		 */
		searchDebounce: { default: 400 },

		/**
		 * String containing all serialized UI changes
		 *
		 * @type {string}
		 */
		serializedChanges: {},

		/**
		 * Unique name for a shadowDom slot
		 *
		 * @type {string}
		 * @private
		 */
		slot: {},

		/**
		 * Name of the table the reference field is attached to
		 *
		 * @type {string}
		 */
		tableName: {},

		/**
		 * Value can be anything
		 *
		 * @type {any}
		 */
		value: {}
	},
	dispatches: {
		/**
		 * Dispatched on each value change on the input.
		 * @type {{ value: string }}
		 */
		'NOW_RECORD_FIELD_LOOKUP_CONNECTED#STAGED_VALUE_CHANGED': {},
		/**
		 * Dispatched when a new option is selected, adding a new pill, or making
		 * a wrong reference selection.
		 * @type {{ value: string, error: { type: string, message: string } }}
		 */
		'NOW_RECORD_FIELD_LOOKUP_CONNECTED#VALUE_CHANGED': {}
	},
	transformState,
	actionHandlers: actionHandlers,
	actionTypesToProps: { VALUE_SELECTED: 'addonValueSelected' }
});
