import { queryBaseActionHandlers } from '@devsnc/sn-glide-query-base';

let query = `
query ($table: String!, $field: String!, $conditions: String, $paginationLimit: Int) {
  GlideLayout_Query {
		listLayout(
			listTableName: $table,
			listColumnNames: $field
		) {
			_graphql(
				queryGeneratorArguments: [{
					name: "fields",
					value: "displayValue"
				}]
			) {
				_result(
					variables: [
						{ name: "table", value: $table },
						{ name: "queryConditions", value: $conditions }
					],
					pagination: {
						limit: $paginationLimit,
					}
				)
			}
    }
  }
}
`;

let variableList = ['table', 'field', 'conditions', 'paginationLimit'];

export default queryBaseActionHandlers(query, variableList);
